// Select all links with hashes
$('a[href*="#"]')
  // Remove links that don't actually link to anything
  .not('[href="#"]')
  .not('[href="#0"]')
  .not('.accordion-toggle')
  .click(function(event) {
    // On-page links
    if (
      location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') 
      && 
      location.hostname == this.hostname
    ) {
      // Figure out element to scroll to
      var target = $(this.hash);
      target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
      // Does a scroll target exist?
      if (target.length) {
        // Only prevent default if animation is actually gonna happen
        event.preventDefault();
        $('html, body').animate({
          scrollTop: target.offset().top
        }, 1000, function() {
          // Callback after animation
          // Must change focus!
          var $target = $(target);
          $target.focus();
          if ($target.is(":focus")) { // Checking if the target was focused
            return false;
          } else {
            $target.attr('tabindex','-1'); // Adding tabindex for elements not focusable
            $target.focus(); // Set focus again
          };
        });
      }
    }
  });

if (matchMedia) {
  var mq = window.matchMedia("(max-width: 991px)");
  mq.addListener(WidthChange);
  WidthChange(mq);
}

function WidthChange(mq) {
	if (mq.matches) {
		$("#second-toggle--title").removeClass('active');
		$("#second-toggle").hide();
		$("#first-toggle--title").addClass('active');
		$("#first-toggle").show();
		$("#first-toggle--title").click(function(){
			console.log('first click');
			$("#second-toggle--title").removeClass('active');
			$("#first-toggle--title").addClass('active');
			$("#second-toggle").hide();
			$("#first-toggle").show();
		});

		$("#second-toggle--title").click(function(){
			$("#first-toggle--title").removeClass('active');
			$("#second-toggle--title").addClass('active');
			$("#first-toggle").hide();
			$("#second-toggle").show();
		});
	} else {
		$("#second-toggle").show();
		$("#first-toggle").show();
		$("#second-toggle--title").removeClass('active');
		$("#first-toggle--title").removeClass('active');
	}
}


function tabClick() {
	$('.tabbed-container').fadeToggle(300);
	$('.tab').toggleClass('active');
}

function tabListener() {
	$('.tab').click(function(){
		tabClick();
	});
	$('.tabbed-container:last-of-type').fadeOut();
}





$(document).ready(function() {
	tabListener();
});